import React, { useEffect, useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import './BottomNavBar.css';
import { auth, db } from '../firebase';
import { doc, getDoc } from 'firebase/firestore';

const BottomNavBar = () => {
  const navigate = useNavigate();
  const location = useLocation();

  const [userPhotoUrl, setUserPhotoUrl] = useState(null);
  const [avatarLoadError, setAvatarLoadError] = useState(false);

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(async (user) => {
      if (!user) {
        setUserPhotoUrl(null);
        setAvatarLoadError(false);
        return;
      }
      try {
        const snapshot = await getDoc(doc(db, 'users', user.uid));
        const data = snapshot.exists() ? snapshot.data() : {};
        const url = data.photoURL || user.photoURL || null;
        setUserPhotoUrl(url);
        setAvatarLoadError(false);
      } catch (_) {
        setUserPhotoUrl(null);
      }
    });
    return () => unsubscribe();
  }, []);

  const navItems = [
    { id: 'home', label: 'Home', icon: 'üè†', path: '/home' },
    { id: 'search', label: 'Search', icon: 'üîç', path: '/search' },
    { id: 'book', label: 'Book', icon: '‚ûï', path: '/book-service' },
    { id: 'chat', label: 'Chat', icon: 'üí¨', path: '/chat' },
    { id: 'profile', label: 'Profile', icon: 'üë§', path: '/profile' }
  ];

  const handleNavClick = (path) => {
    navigate(path);
  };

  return (
    <nav className="bottom-navbar">
      <div className="nav-container">
        {navItems.map(item => (
          <button
            key={item.id}
            className={`nav-item ${location.pathname === item.path ? 'active' : ''}`}
            onClick={() => handleNavClick(item.path)}
          >
            <span className="nav-icon">
              {item.id === 'profile' && userPhotoUrl && !avatarLoadError ? (
                <img
                  src={userPhotoUrl}
                  alt="Profile"
                  className="nav-avatar"
                  onError={() => setAvatarLoadError(true)}
                />
              ) : (
                item.icon
              )}
            </span>
            <span className="nav-label">{item.label}</span>
          </button>
        ))}
      </div>
    </nav>
  );
};

export default BottomNavBar;

